.title{font-size:24px;font-weight:600}.titleAndText .title{margin:0 0 16px 0}.image{position:relative;z-index:0;margin:0 auto}.image img{width:100%}.image.big{width:100%}.image.big img{border-radius:12px}.image.medium{width:75%}.image.medium img{border-radius:10px}.image.small{width:50%}.image.small img{border-radius:8px}.twoImage{display:flex;gap:20px}.twoImage img{width:calc((100% - 20px)/2);border-radius:8px}.blockquote{padding:24px;font-style:italic;background-color:var(--beige-1);border-radius:12px;border-left:4px solid var(--beige-2)}.blockquote::before{content:"“"}.blockquote::after{content:"”"}.list .text{margin:0 0 12px 0}.list ul{display:flex;flex-direction:column;gap:12px;margin:0 0 0 16px}.list ul li{position:relative}.list ul li::before{content:"";position:absolute;top:6px;left:-15.5px;height:6px;width:6px;rotate:45deg;background-color:var(--black)}.list ol{display:flex;flex-direction:column;gap:12px;margin:0 0 0 18px}