.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin: auto;
  .loading__spinner {
    width: 50px;
    height: 50px;
    border-color: rgba(0, 0, 0, 0.1);
    border-style: solid;
    border-width: 5px;
    border-top-color: var(--black);
    border-radius: 50%;
    animation: spin 1s ease-in-out infinite;
  }
  &.isPage {
    min-height: calc(100vh - 80px);
  }
  &.isWrapperContent {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
